import topSearch from '../../components/topSearch.vue'

export default {
	name: "topSearch",
	components: {
		topSearch
	},
	data() {
		return {
			mapName:"萧山区",
			windowW:'',
			banner:{    //首页banner轮播
				indicatorDots: true,
				autoplay: true,
				circular:true,
				interval: 6000,
				duration: 500,
				displayItems: 1,
				previousM: '',
				nextM: '',
				indicatorColorOff:"#999999",
				indicatorColorOn:"#C90009"
			},
			notice:{    //活动公告
				autoplay:true,
				interval:5000,
				duration:300,
				circular:true,
				vertical:true
			},
			hdzx:{    //活动资讯
				autoplay:false,
				circular:true,
				interval: 6000,
				duration: 500,
				displayItems: 1,
				previousM:'',
				nextM: '',
			},
			syzx:{    //商业资讯
				autoplay:false,
				circular:true,
				interval: 6000,
				duration: 500,
				displayItems: 2,
				nextM: '',
			},
			banner_list:[],
			contentList1:[],
			contentList2:[],
		}
	},
	created(){
		let _this=this;
	},
	mounted(){
		let _this=this;
		//轮播自适应
		uni.getSystemInfo({
			success: function (res) {
				_this.windowW=res.windowWidth;
				_this.banner.previousM=_this.windowW*13/375;
				_this.banner.nextM=_this.windowW*20/375;
				_this.hdzx.previousM=_this.windowW*0/375;
				_this.hdzx.nextM=_this.windowW*85/375;
				_this.syzx.nextM=_this.windowW*0/375;
			}
		});
		
		//baner
		_this.getBanner();	
		_this.events1();
		_this.events2();
	},
	methods: {
		goClassPages(url,id){
			if(id!=''){
				url = url+'?id='+id
			}
			uni.navigateTo({
				url:url
			})
		},
		//banner
		getBanner:function(){
			let _this=this;
			let data={
				id:2,
			};
			_this.$axios(_this.$baseUrl.banner,data).then(res =>{
				if(res.data.status==1){
					_this.banner_list = res.data.result.banner;
				}
			}).catch(error =>{
				
			})
		},
		// 活动列表
		events1:function(){
			let _this=this;
			let data={
				type:7,
			};
			_this.$axios(_this.$baseUrl.events1,data).then(res =>{
				if(res.data.status==1){
					_this.contentList1 = res.data.result.articles;
				}
			}).catch(error =>{
				
			})
		},
		// 商业列表
		events2:function(){
			let _this=this;
			let data={
				type:8,
			};
			_this.$axios(_this.$baseUrl.events1,data).then(res =>{
				if(res.data.status==1){
					_this.contentList2 = res.data.result.articles;
				}
			}).catch(error =>{
				
			})
		},
		childMethod() {
		},
	},
	
}
